.l-center{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
}

.l-full-size{
    width: 100vw;
    height: 100vh;
}

.l-center-row{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
}


.l-footer{
    width: 100vw;
    height: 200px;
    display: flex;
    justify-content: center;
    background-color: rgb(0, 0, 0);
    color: white;
}

@media screen and (max-width: 960px){
    .l-footer{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}