.text-fall{
    animation: fall 0.6s ease-out forwards, spin 0.6s ease-out forwards;
}

.text-hidden{
    position: relative;
    transform: translateX(calc(-35 * 20px/1.618));
    animation: textHidden 0.6s ease-out forwards 0.6s;
}

.slide{
    animation:slide 3s ease-in-out infinite;
}

.slide-right{
    animation:slideRight 1.5s ease-in-out forwards;
}

.smooth-transition{
    animation: smooth 0.8s linear forwards;
}


@keyframes fall {
    0%{
        top: -32.36px;
    }

    100%{
        top: 50%;
        transform: translateY(-16.18px);
    }
}


@keyframes spin {
    0%{
       transform: rotate(180deg);
    }

    100%{
        transform: rotate(360deg);
    }

}

@keyframes textHidden{
    0%{
        transform: translateX(-432.632880099px);
    }

    100%{
        transform: translateX(50%) translateX(-216.316440049px)
    }
}


@keyframes slide {
    0% {top:0px;}
    33% {top:-100px;}
    66% {top:-200px;}
    100% {top:-300px;}
}

@keyframes slideRight {
    0%{
        left: -250px;
    }
    
    80%{
        left:50%;
        transform: translateX(-125px)
    }

    90%{
        left: 50%;
        transform: translateX(-118.5px);
    }

    100%{
        left:50%;
        transform: translateX(-125px)
    }
}

@keyframes smooth{
    0%{
        opacity: 0;
        transform: rotateX(-10deg);
    }

    100%{
        opacity: 1;
        transform: rotateX(0deg);
    }
}


@media screen and (max-width:960px){

    @keyframes fall {
        0%{
            top: -25.888px
        }
        

        100%{
            top: 50%;
            transform: translateX(-12.944px)
        }
    }

    @keyframes textHidden{
        0%{
            transform: translateX(-296.662546354px);
        }
    
        100%{
            transform: translateX(50%) translateX(-148.331273177px);
        }
    }
}