html{
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
}

body{
    overflow: auto;
    overflow: initial;
    overflow-x: hidden;
}

html,body{
    margin: 0;
    width: 100vw;
    height: 100vh;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

a{
    text-decoration: none;
    color: inherit;
}


@media (max-width: 960px){
    html{
        font-size: 16px;
    }
}