.m-nav{
    position: fixed;
    z-index: 4;
    width: 100vw;
    height: 60px;
    background-color: rgba(0, 0, 0, 0.76);
    color: white;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.m-nav ul{
    display: flex;
    list-style: none;
    margin-right: 20px;
}

.m-nav ul a{
    padding: 5px;
    margin-left: 5px;
    width: 80px;
    position: relative;
    text-align: center;
}


.m-nav a:before, .m-nav a:after
{
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0px;
    content: '';
    background: rgb(255, 255, 255);
    opacity: 0.3;
    transition: all 0.3s;
}

.m-nav a:before
{
    top: 0px;
} 

.m-nav a:after
{
    bottom: 0px;
}

.m-nav a:hover:before, .m-nav a:hover:after
{
    height: 100%;
}


.m-nav ul a:hover{
    cursor: pointer;
}


.m-text-center{
    text-align: center;
    position: absolute;
    overflow: hidden;
    width: calc(35 * 20px/1.618);
    top: calc(-20px * 1.618);
    line-height: calc(20px * 1.618);
}

.m-center{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
}

.m-center.places{
    padding-top: 10px;
    padding-bottom: 10px;
}

.m-center.about{
    border-top: 1px solid rgba(0, 0, 0, 0.185);
    height: 50vh;
}

.m-place{
    position: relative;
    box-shadow: 5px 5px 3px 1px rgba(0, 0, 0, 0.473);
}

.m-place .info{
    position: absolute;
    transition: height 0.3s linear;
    color: white;
    height: 20px;
    overflow:hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.438);
}

.m-place .info .icon {
    transition: transform 0.3s ease-in;
    transform: rotateZ(0deg);
    position: absolute;
    top: 0;
}

.m-place .info ul{
    list-style: none;
    margin: 0;
    padding:0;
}

.m-place .info ul li{
    padding: 5px;
    text-align: center;
    border-bottom: 1px solid rgba(255, 255, 255, 0.356);
}

.m-place:hover .info,.m-place:active .info{
    height: 100%;
}

.m-place:hover .info .icon, .m-place:focus .info .icon {
    transform: rotateZ(180deg);
}

.m-place:hover .info ul, .m-place:focus .info ul{
    display: block;
}

.m-place .info ul{
    display: none;
}

.m-center-row{
    display: flex;
    justify-content: center;
    width: 100vw;
    height: 100vh;
}

.m-grid{
    display: grid;
    grid-template-columns: 300px 300px 300px;
    grid-template-rows: 200px auto;
    grid-gap: 10px;
}

.m-place img{
    display: block;
}


.m-slider-wrapper {
    font-size: 1.5em;
    color:#aaa;
    font-weight: bold;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .m-slider{
    position: relative;
    height: 50px;
    padding-left:15px;
  }

  .m-slider .m-wrap-text{
      position: relative;
      top: 0px;
  }

  .m-slider .m-wrap-text div {
    color:#fff;
    padding: 10px;
    height: 50px;
    margin-bottom: 50px;
    text-align: center;
    box-sizing: border-box;
}

.m-slider .m-wrap-text div{
    display: flex;
    align-items: center;
    justify-content: center;
}

.m-slider{
    height: 50px;
    padding-left:15px;
    overflow: hidden;
}

.m-slider-text {
    background: rgb(0, 0, 0);
    color: white;
}

.m-place-intro{
    width: 100vw;
    height: 100vh;
    position: relative;
    display: flex;
    align-items: center;
    color: white;
}


.m-place-main-text{
    position: absolute;
    text-align: center;
    width: 250px;
    font-size: 1.3rem;
    padding: 10px;
    left: -250px;
    background-color: rgb(0, 0, 0);
}

.m-place-article{
    text-align: center;
    line-height: calc(20px * 1.618);
    width:calc(68 * (20px/1.618));
}

.m-foot-info{
    text-align: center;
    width: 300px;
}

.m-foot-info:first-child{
    border-right: 1px solid rgba(255, 255, 255, 0.568);
}


@media screen and (max-width: 960px){
   .m-text-center{
      line-height: calc(16px * 1.618);
      width: calc(30 * 16px/1.618);
   }

   .m-place-article{
      line-height: calc(16px * 1.618);
      width:calc(45 * 16px/1.618);
   }
   
   .m-grid{
       grid-template-columns: 300px;
   }

   .m-foot-info:first-child{
       border-right: none;
       border-bottom: 1px solid rgba(255, 255, 255, 0.568);
   }
}